import React, { Component } from 'react';
import * as ReactRedux from 'react-redux';
import { addTeam } from '../actions/teamActions';
import { constants } from '../constants/index';
import * as HttpClient from 'superagent';
import { Redirect } from 'react-router-dom';
import { Row, Col, Input, Button } from 'react-materialize';
import { bindActionCreators } from 'redux';

class TeamUI extends Component {

    constructor(props) {
        super(props);

        this.state = {
            teamName: null
        };
    }

    handleTeamNameChange(evt) {
        this.setState({ ...this.state, teamName: evt.target.value });
    }

    handleSubmit() {
        if (this.state.teamName !== null && this.state.teamName !== '') {
            const payload = {
                teamName: this.state.teamName
            };

            HttpClient
                .post(constants.serverUrls.root + constants.serverUrls.team + this.props.kwizz.id, payload)
                .then((res) => {
                    if (res.ok)
                        this.props.addTeam(res.body);
                    else
                        window.Materialize.toast(res.body, 5000);
                })
                .catch((err) => {
                    window.Materialize.toast(err.message, 5000);
                });
        } else {
            window.Materialize.toast('Please provide a name.', 5000);
        }
    }

    render() {
        const handleTeamNameChange = this.handleTeamNameChange.bind(this);
        const handleSubmit = this.handleSubmit.bind(this);

        if (!this.props.kwizz.id) return <Redirect to={constants.routes.home}></Redirect>;
        if (this.props.team._id) return <Redirect to={constants.routes.pending}></Redirect>;

        return (
            <div className='team'>
                <h1 className='head'>{this.props.kwizz.name}</h1>
                <Row>
                    <span>welcome! Enter a fitting name for your team</span>
                </Row>

                <Row>
                    <Col s={12} m={6} offset="m3 s0">
                        <Input m={12} s={12} label="Your Team Name" onChange={handleTeamNameChange} value={this.state.teamName ? this.state.teamName : ''}></Input>
                    </Col>
                </Row>

                <Row>
                    <Button waves="light" onClick={handleSubmit}>Enter Kwizz</Button>
                </Row>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        kwizz: state.kwizzReducer.kwizz,
        team: state.teamReducer.team
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        addTeam
    }, dispatch);
}

export const Team = ReactRedux.connect(mapStateToProps, mapDispatchToProps)(TeamUI);


