import React, { Component } from 'react';
import * as HttpClient from 'superagent';
import { constants } from '../constants/index';
import { bindActionCreators } from 'redux';
import { openWebSocket } from '../communication/socket';
import * as ReactRedux from 'react-redux';
import { addKwizz } from '../actions/kwizzActions';
import { addTeam } from '../actions/teamActions';
import { Row, Col, Input, Button, Card } from 'react-materialize';
import { refresh } from '../actions/teamActions';
import { Redirect } from 'react-router-dom';

class PendingUI extends Component {
    constructor(props) {
        super(props);

        this.state = {
            teamName: null,
            socket: null
        };
    }

    componentWillMount() {
        this.setState({ ...this.state, socket: openWebSocket() });
    }

    componentDidMount() {
        this.state.socket.emit('roomJoin', this.props.kwizz.id);
        this.openSocket();
    }

    openSocket() {
        this.state.socket.on('message', (msg) => {
            console.log(msg)
            switch (msg) {
                case constants.socketMessages.changeTeam:
                    this.refreshKwizz();
                    break;
                case constants.socketMessages.startKwizz:
                    this.refreshKwizz();
                    break;
                default:
                    break;
            }
        });
    }

    refreshKwizz() {
        HttpClient
            .get(`${constants.serverUrls.root}${constants.serverUrls.kwizz}${this.props.kwizz.id}`)
            .then((res) => {
                if (res.ok)
                    this.props.refresh(res.body, this.props.team._id);
                else
                    window.Materialize.toast(res.body, 5000);
            })
            .catch((err) => {
                window.Materialize.toast(err.message, 5000);
            });
    }

    handleSubmit() {
        const payload = {
            teamName: this.state.teamName
        };

        HttpClient
            .put(`${constants.serverUrls.root}${constants.serverUrls.team}${this.props.kwizz.id}/${this.props.team._id}`, payload)
            .then((res) => {
                if (res.ok) {
                    this.refreshKwizz();
                    console.log(res)
                    this.props.addKwizz(res.body);
                } else
                    window.Materialize.toast(res.body, 5000);
            })
            .catch((err) => {
                window.Materialize.toast(err.message, 5000);
            });
    }

    handleTeamNameChange(evt) {
        this.setState({ ...this.state, teamName: evt.target.value });
    }

    render() {
        const handleTeamNameChange = this.handleTeamNameChange.bind(this);
        const handleSubmit = this.handleSubmit.bind(this);

        if (!this.props.team._id) return <Redirect to={constants.routes.home}></Redirect>;
        if (this.props.team.status === '#99F2A9' && this.props.kwizz.started) return <Redirect to={constants.routes.kwizz}></Redirect>;

        if (this.props.kwizz.started && this.props.team.status === constants.teamColors.pending)
            return <div><Card className='grey lighten-3' title='Sorry!'>
                It seems like the Kwizz has already started without you. You can no longer join the Kwizz.
            </Card></div>;

        if (this.props.kwizz.started && this.props.team.status === constants.teamColors.denied)
            return <div><Card className='grey lighten-3' title='Sorry!'>
                The KwizzMaster has denied your entry and has started the Kwizz. You can no longer join.
        </Card></div>;

        return (
            <div className='pending'>
                <h1 className='head'>{this.props.kwizz.name}</h1>
                <Row>
                    <span>Wait for the KwizzMaster to approve your team. You can still change your team name if you want.</span>
                </Row>

                <Row>
                    <Col s={12} m={6} offset="m3 s0">
                        <Input m={12} s={12} label="Change team name" onChange={handleTeamNameChange} value={this.state.teamName ? this.state.teamName : ''}></Input>
                    </Col>
                </Row>

                <Row>
                    <Button waves="light" onClick={handleSubmit}>Change Team name</Button>
                </Row>

                <hr />
                <Row>
                    <Col m={6} s={12} offset='m3'>
                        <Card style={{ backgroundColor: this.props.team.status }} textClassName='white-text' title={this.props.team.name}>
                        </Card>
                    </Col>
                </Row>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        kwizz: state.kwizzReducer.kwizz,
        team: state.teamReducer.team
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        addTeam,
        addKwizz,
        refresh
    }, dispatch);
}

export const Pending = ReactRedux.connect(mapStateToProps, mapDispatchToProps)(PendingUI);


