import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import * as ReactRedux from 'react-redux';
import { constants } from '../constants/index';
import { openWebSocket } from '../communication/socket';
import { Table, Row, Col, CardPanel } from 'react-materialize';
import { Redirect } from 'react-router-dom';
import { refresh } from '../actions/teamActions';
import * as HttpClient from 'superagent';

class ScoresUI extends Component {
    constructor(props) {
        super(props);

        this.state = {
            socket: null,
        }
    }

    componentWillMount() {
        this.setState({ ...this.state, socket: openWebSocket() });
    }

    componentDidMount() {
        this.state.socket.emit('roomJoin', this.props.kwizz.id);
        this.openSocket();
    }

    openSocket() {
        this.state.socket.on('message', (msg) => {
            switch (msg) {
                case constants.socketMessages.newRound:
                    this.refreshKwizz();
                    break;
                case constants.socketMessages.endKwizz:
                    this.refreshKwizz();
                    break;
                default:
                    break;
            }
        });
    }

    refreshKwizz() {
        HttpClient
            .get(`${constants.serverUrls.root}${constants.serverUrls.kwizz}${this.props.kwizz.id}`)
            .then((res) => {
                if (res.ok)
                    this.props.refresh(res.body, this.props.team._id);
                else
                    window.Materialize.toast(res.body, 5000);
            })
            .catch((err) => {
                window.Materialize.toast(err.message, 5000);
            });
    }

    render() {
        if (!this.props.kwizz.id) return <Redirect to={constants.routes.home}></Redirect>;
        if (!this.props.kwizz.endOfRound) return <Redirect to={constants.routes.kwizz}></Redirect>;

        let winningTeam = null;

        const teams = this.props.kwizz.teams.map((team) => {
            if (winningTeam === null || team.correctAnswers > winningTeam.correctAnswers)
                winningTeam = team;

            return <tr>
                <td>{team.name}</td>
                <td>{team.points}</td>
            </tr>;
        });

        const winnerMessage = this.props.kwizz.finished ? <Col s={10} m={10} offset='s1 m1'>
            <h3>The Kwizz has ended. The winning team is <b>{winningTeam.name}</b> !</h3>
        </Col> : <Col s={10} m={10} offset='s1 m1'>
                <h3>The winning team this round is <b>{winningTeam.name}</b> !</h3>
                <span>The KwizzMaster can still start a new round. Wait for his confirmation.</span>
            </Col>;

        const infoMessage = this.props.kwizz.finished ? <CardPanel>
            <Row><span>This Kwizz lasted <b>{this.props.kwizz.currentRound}</b> rounds.</span></Row>
            <Row><span>You had <b>{this.props.team.correctAnswers}</b> correct questions.</span></Row>
        </CardPanel> : <CardPanel>
                <Row><span>This is round <b>{this.props.kwizz.currentRound}</b>.</span></Row>
                <Row><span>You have <b>{this.props.team.correctAnswers}</b> correct questions so far.</span></Row>
            </CardPanel>;

        return (
            <div>
                <Row>
                    {winnerMessage}
                </Row>

                <Row>
                    <Col s={10} m={4} offset='s1 m4'>
                        <Table responsive={true} striped={true}>
                            <thead>
                                <tr>
                                    <th data-field="teamName">Team</th>
                                    <th data-field="scores">Score</th>
                                </tr>
                            </thead>

                            <tbody>
                                {teams}
                            </tbody>
                        </Table>
                    </Col>
                </Row>

                <Row>
                    <Col s={10} m={4} offset='s1 m4'>
                        {infoMessage}
                    </Col>
                </Row>

            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        kwizz: state.kwizzReducer.kwizz,
        team: state.teamReducer.team
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        refresh
    }, dispatch);
}

export const Scores = ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ScoresUI);


