import React, { Component } from 'react';
import * as ReactRedux from 'react-redux';
import '../styles/Home.css';
import { addKwizz } from '../actions/kwizzActions';
import { Redirect } from 'react-router-dom';
import { Row, Col, Button, Input } from 'react-materialize';
import { bindActionCreators } from 'redux';
import * as HttpClient from 'superagent';
import { constants } from '../constants/index';

class HomeUI extends Component {

    constructor(props) {
        super(props);

        this.state = {
            kwizzId: null
        };
    }

    handleIDchange(evt) {
        this.setState({ ...this.state, kwizzId: evt.target.value });
    }

    handleSubmit() {
        if (this.state.kwizzId !== null && this.state.kwizzId !== '') {

            HttpClient
                .get(`${constants.serverUrls.root}${constants.serverUrls.kwizz}${this.state.kwizzId}`)
                .then((res) => {
                    if (res.status !== 204) {
                        this.props.addKwizz(res.body);
                    } else {
                        window.Materialize.toast(res.body, 5000);
                    }
                })
                .catch((err) => {
                    window.Materialize.toast(`${err.message}, you probably did not enter an ID. `, 5000);
                });
        } else {
            window.Materialize.toast('Please provide a Kwizz ID.', 5000);
        }
    }

    render() {
        const handleSubmit = this.handleSubmit.bind(this);
        const handleIDchange = this.handleIDchange.bind(this);

        if (this.props.kwizz.id) return <Redirect to={constants.routes.team}></Redirect>;

        return (
            <div className='home'>
                <h1 className='head'>Kwizzer</h1>
                <Row>
                    <span>to start, enter the ID of your Kwizz and press start</span>
                </Row>

                <Row>
                    <Col s={12} m={6} offset="m3 s0">
                        <Input m={12} s={12} label="Your Kwizz ID" onChange={handleIDchange} value={this.state.kwizzId ? this.state.kwizzId : ''}></Input>
                    </Col>
                </Row>

                <Row>
                    <Button waves="light" onClick={handleSubmit}>Start</Button>
                </Row>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        kwizz: state.kwizzReducer.kwizz,
        team: state.teamReducer.team
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        addKwizz
    }, dispatch);
}

export const Home = ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HomeUI);


