import React, { Component } from 'react';
import { openWebSocket } from '../communication/socket';
import * as HttpClient from 'superagent';
import { bindActionCreators } from 'redux';
import * as ReactRedux from 'react-redux';
import { constants } from '../constants/index';
import { refresh } from '../actions/teamActions';
import { Redirect } from 'react-router-dom';
import '../styles/Kwizz.css';
import { Row, Col, Input, CardPanel, Button, Card } from 'react-materialize';

class KwizzUI extends Component {
    constructor(props) {
        super(props);

        this.state = {
            socket: null,
            answer: null,
            submit: false,
        };
    }

    componentWillMount() {
        this.setState({ ...this.state, socket: openWebSocket() });
    }

    componentDidMount() {
        this.state.socket.emit('roomJoin', this.props.kwizz.id);
        this.openSocket();
    }

    openSocket() {
        this.state.socket.on('message', (msg) => {
            console.log(msg)
            switch (msg) {
                case constants.socketMessages.question:
                    this.refreshKwizz();
                    break;
                case constants.socketMessages.changeAnswerStatus:
                    this.refreshKwizz();
                    break;
                case constants.socketMessages.closeQuestion:
                    this.setState({ ...this.state, submit: false });
                    this.refreshKwizz();
                    break;
                case constants.socketMessages.endRound:
                    this.refreshKwizz();
                    break;
                default:
                    break;
            }
        });
    }

    refreshKwizz() {
        HttpClient
            .get(`${constants.serverUrls.root}${constants.serverUrls.kwizz}${this.props.kwizz.id}`)
            .then((res) => {
                if (res.ok)
                    this.props.refresh(res.body, this.props.team._id);
                else
                    window.Materialize.toast(res.body, 5000);
            })
            .catch((err) => {
                window.Materialize.toast(`${err.message}, something went wrong while refreshing.`, 5000);
            });
    }

    handleAnswerChange(evt) {
        this.setState({ ...this.state, answer: evt.target.value });
    }

    handleSubmit() {
        const payload = {
            answer: this.state.answer,
            change: this.state.submit
        };

        HttpClient
            .put(`${constants.serverUrls.root}${constants.serverUrls.kwizz}${this.props.kwizz.id}${constants.serverUrls.team}${this.props.team._id}${constants.serverUrls.question}`, payload)
            .then((res) => {
                if (res.ok) {
                    this.refreshKwizz();
                    this.setState({ ...this.state, answer: null, submit: true });
                } else
                    window.Materialize.toast(res.body, 5000);
            })
            .catch((err) => {
                window.Materialize.toast(err.message, 5000);
            });
    }

    render() {
        if (!this.props.kwizz.id) return <Redirect to={constants.routes.home}></Redirect>;

        const handleAnswerChange = this.handleAnswerChange.bind(this);
        const handleSubmit = this.handleSubmit.bind(this);

        if (this.props.kwizz.endOfRound) return <Redirect to={constants.routes.scores}></Redirect>;
        if (!this.props.kwizz.currentQuestion) return <Row className='loading'><Col s={12} m={12}><h4>Waiting for the KwizzMaster to pick a question..</h4></Col></Row>;

        const submitted = this.state.submit ? <Row>
            <Col s={12} m={12}>
                <span>Wait for the KwizzMaster to approve or deny your answer. You can still change your answer if you want.</span>
            </Col>
        </Row> : null;

        const submittedAnswer = this.state.submit ? <div><hr /><Row>
            <Col m={6} s={12} offset='m3'>
                <Card style={{ backgroundColor: this.props.team.answerStatus }} textClassName='white-text' title={this.props.team.answer}>
                </Card>
            </Col>
        </Row></div> : null;

        return (
            <div className='kwizz'>
                <Row>
                    <Col s={10} m={6} offset='s1 m3'>
                        <CardPanel className='question_card'>
                            <Row><span className='question'>{this.props.kwizz.currentQuestion.question}</span></Row>
                            <Row><span className='category'>{this.props.kwizz.currentQuestion.category}</span></Row>
                        </CardPanel>
                    </Col>
                </Row>
                {submitted}
                <Row>
                    <Col s={10} m={8} offset='s1 m2'>
                        <Input m={12} s={12} label="Your answer" onChange={handleAnswerChange} value={this.state.answer ? this.state.answer : ''}></Input>
                    </Col>
                </Row>
                <Row>
                    <Button waves='light' onClick={handleSubmit}>Send Answer</Button>
                </Row>

                {submittedAnswer}
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        kwizz: state.kwizzReducer.kwizz,
        team: state.teamReducer.team
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        refresh
    }, dispatch);
}

export const Kwizz = ReactRedux.connect(mapStateToProps, mapDispatchToProps)(KwizzUI);


