import React, { Component } from 'react';
import '../styles/App.css';
import { Switch, Route } from 'react-router-dom';
import { constants } from '../constants/index';
import { Home } from '../components/Home';
import { Team } from '../components/Team';
import { Pending } from '../components/Pending';
import { Kwizz } from './Kwizz';
import { Scores } from './Scores';

class App extends Component {
  render() {
    return (
      <div className="App">

        <Switch>
          <Route exact path={constants.routes.home} component={Home}></Route>
          <Route path={constants.routes.team} component={Team}></Route>
          <Route path={constants.routes.pending} component={Pending}></Route>
          <Route path={constants.routes.kwizz} component={Kwizz}></Route>
          <Route path={constants.routes.scores} component={Scores}></Route>
        </Switch>

      </div>
    );
  }
}

export default App;



