import { constants } from '../constants/index';

const initialState = {
    team: {}
};

export function teamReducer(state = initialState, action) {
    switch (action.type) {
        case constants.teamActions.addTeam:
            return { ...state, team: action.payload.newTeam };
        case constants.teamActions.refresh:
            const team = action.kwizz.teams.find(team => team._id === action.id);
            return { ...state, team: team };
        default:
            return state;
    }
}


