import { constants } from '../constants/index';

const initialState = {
    kwizz: {}
};

export function kwizzReducer(state = initialState, action) {
    switch (action.type) {
        case constants.kwizzActions.addKwizz:
            return { ...state, kwizz: action.payload };
        case constants.teamActions.addTeam:
            return { ...state, kwizz: action.payload.kwizz };
        case constants.teamActions.refresh:
            return { ...state, kwizz: action.kwizz };
        default:
            return state;
    }
}




